@import './../../../../styles/variables.scss';


.this_day_info{
	position: relative;
	padding: 40px;
	max-width: 750px;
	width: 100%;
	background:  var(#{--components-background-default});
	box-shadow: $box-shadow;
	border-radius:$border-radius;
}

.item{
	position: relative;
	display: flex;
	align-items: center;
	margin-bottom: 25px;
	&:last-of-type{
		margin-bottom: 0;
	}
	.indicator{
		width: 38px;
		height: 38px;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color:#fff ;
		box-shadow: 1px 4px 10px -1px rgba(18,18,18,0.4);
		border-radius: 50%;
		margin-right: 20px;

	}
	.indicator_name{
		font-size: 14px;

		margin-right: 20px;
		color:$base-color;
	}
	.indicator_value{
		font-size: 14px;
		color:var(#{--text-color-default});
	}
}
.cloud{
	position: absolute;
	top: 0;
	right: 0;

}