@import './../../styles/variables.scss';

.header{

	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 100%;
	margin-bottom: 30px;
}


.wrapper{
	display: flex;
	align-items: center;
}
.change_theme{
	cursor: pointer;
}
.logo{
	margin-right: 20px;
	cursor: pointer;
	svg{
		width: 65px;
		height: 65px;
	}
}
.title{
	color:$primary-color;
	text-transform: uppercase;
	font-size: 25px;
	font-weight: bold;
}


